'use client';

import React, { useState, useEffect } from 'react';
import { supabase } from '@/lib/supabase';
import { 
  ShieldCheck, Cpu, Globe, Send, Loader2, 
  FileText, Sparkles, AlertCircle 
} from 'lucide-react';

export default function IntelligenceLab() {
  const [loading, setLoading] = useState(true);
  const [isPro, setIsPro] = useState(false);
  const [query, setQuery] = useState('');
  const [analyzing, setAnalyzing] = useState(false);
  const [result, setResult] = useState<string | null>(null);

  useEffect(() => {
    const checkAccess = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      if (user) {
        const { data } = await supabase.from('profiles').select('is_pro').eq('id', user.id).single();
        if (data) setIsPro(data.is_pro);
      }
      setLoading(false);
    };
    checkAccess();
  }, []);

  if (loading) return <div className="min-h-screen flex items-center justify-center"><Loader2 className="animate-spin text-[#D4AF37]" /></div>;

  // Proteksi Halaman: Jika bukan Pro, arahkan ke halaman Pro
  if (!isPro) {
    return (
      <div className="min-h-screen bg-[#FDFCFB] flex items-center justify-center p-8">
        <div className="max-w-md text-center space-y-6">
          <AlertCircle className="w-16 h-16 text-red-400 mx-auto" />
          <h1 className="text-3xl font-serif italic">Access Restricted.</h1>
          <p className="text-gray-400 font-light italic">Halaman Intelligence Lab hanya tersedia untuk anggota Pro Lifetime Access.</p>
          <button className="px-8 py-3 bg-black text-[#D4AF37] rounded-full text-[12px] font-black uppercase tracking-widest">Upgrade Now</button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black font-sans pb-32">
      <section className="max-w-6xl mx-auto pt-24 pb-12 px-8">
        <div className="space-y-4">
          <div className="inline-flex items-center gap-2 px-4 py-1.5 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.3em]">
            <ShieldCheck className="w-4 h-4" /> Pro Intelligence Lab
          </div>
          <h1 className="text-5xl font-serif italic leading-tight text-black">Treaty <span className="text-[#D4AF37]">Simulation.</span></h1>
          <p className="text-[16px] text-gray-400 font-light italic leading-relaxed">Execute cross-border tax analysis using your private intelligence vault.</p>
        </div>
      </section>

      {/* Mesin Analisis Utama */}
      <section className="max-w-6xl mx-auto px-8">
        <div className="bg-white border border-gray-100 rounded-[3rem] p-12 shadow-2xl shadow-gray-100/50 space-y-12">
          <div className="grid md:grid-cols-3 gap-8">
            <div className="space-y-2">
              <label className="text-[12px] font-black uppercase tracking-widest text-gray-400 ml-2">Home Jurisdiction</label>
              <select className="w-full bg-[#FDFCFB] border border-gray-100 rounded-2xl py-4 px-6 text-[14px] focus:outline-none focus:border-[#D4AF37]">
                <option>USA (United States)</option>
                <option>UK (United Kingdom)</option>
              </select>
            </div>
            <div className="space-y-2 text-center flex items-end justify-center pb-4">
              <Globe className="text-[#D4AF37] opacity-20" size={32} />
            </div>
            <div className="space-y-2">
              <label className="text-[12px] font-black uppercase tracking-widest text-gray-400 ml-2">Target Jurisdiction</label>
              <select className="w-full bg-[#FDFCFB] border border-gray-100 rounded-2xl py-4 px-6 text-[14px] focus:outline-none focus:border-[#D4AF37]">
                <option>Indonesia (IDN)</option>
                <option>Singapore (SGP)</option>
              </select>
            </div>
          </div>

          <div className="space-y-4">
            <label className="text-[12px] font-black uppercase tracking-widest text-gray-400 ml-2">Specific Scenario / Query</label>
            <textarea 
              rows={4}
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              className="w-full bg-[#FDFCFB] border border-gray-100 rounded-3xl py-6 px-8 text-[16px] focus:outline-none focus:border-[#D4AF37] transition-all italic font-light"
              placeholder="e.g. Analyze withholding tax on royalties for a software license from USA to Indonesia..."
            />
          </div>

          <div className="flex justify-between items-center">
             <div className="flex items-center gap-2 text-[12px] text-gray-400 italic">
               <Cpu size={14} /> Engine: <span className="text-black font-bold">GPT-5.2 / o3</span>
             </div>
             <button 
               onClick={() => setAnalyzing(true)}
               className="px-14 py-5 bg-black text-[#D4AF37] rounded-2xl text-[14px] font-black uppercase tracking-[0.2em] hover:bg-[#D4AF37] hover:text-black transition-all shadow-xl shadow-gold/10 flex items-center gap-4"
             >
               {analyzing ? <Loader2 className="animate-spin" /> : <><Sparkles size={18} /> Execute Analysis</>}
             </button>
          </div>
        </div>

        {/* Hasil Analisis Placeholder */}
        {analyzing && (
          <div className="mt-12 bg-black text-[#FDFCFB] rounded-[3rem] p-12 space-y-8 animate-in fade-in slide-in-from-bottom duration-700">
            <div className="flex items-center gap-4 border-b border-white/10 pb-6">
              <FileText className="text-[#D4AF37]" />
              <h2 className="text-[14px] font-black uppercase tracking-[0.4em]">Intelligence Report Generated</h2>
            </div>
            <div className="font-light text-[16px] leading-relaxed space-y-6 opacity-80 italic">
              <p>Berdasarkan Tax Treaty (P3B) USA-Indonesia Pasal 13 (Royalti)...</p>
              <p>Analisis menunjukkan tarif efektif sebesar 10% dengan persyaratan dokumentasi Form DGT...</p>
            </div>
            <button className="flex items-center gap-2 text-[12px] font-black uppercase tracking-widest text-[#D4AF37] hover:underline">
              Download Full PDF Report
            </button>
          </div>
        )}
      </section>
    </div>
  );
}